const utils = require('./utils.js');

function doSearch(queryString) {
    queryString = ".*" + queryString + ".*";
    const clientPromise = stitch.StitchClientFactory.create('wwe-champions-guide-pgwcj');
    clientPromise.then(client => {
        const db = client.service('mongodb', 'mongodb-atlas').db('wwe-champions');
        client.login().then(() =>
            db.collection('players').find({"name": {"$regularExpression": {"pattern":queryString, "options":"i"} } }).execute()
        ).then(docs => {
            $("#loading").addClass('hidden');
            if (docs && docs.length > 0) {
                displayResults(docs)
            } else {
                displayNoResults();
            }
        }).catch(err => {
            $("#loading").addClass('hidden');
            console.error(err)
        });
    });
}

function displayNoResults() {
    console.log("No results have been found!");
    $("#results").empty();
    $("#results").append(
        $("<h4/>", {text: "No results."})
    );
}

function displayResults(items) {
    console.log("Found the following: ");
    $("#results").empty();
    var $listGroup = $("<div/>", {class: "list-group", style: "margin-top: 30px;"});
    items.forEach(function(item){
        $listGroup.append($("<a/>", {class: "list-group-item", href: "/stats/players/profile.html?id=" + item._id, text: item.name}));
        console.log(item);
    });
    $("#results").append($listGroup);
}

$(document).ready(function () {
    var query = utils.getUrlParameter('q');
    if (query) {
        console.log("Searching for " + query);
        $("#loading").removeClass('hidden');
        $('input.search-box').val(query);
        doSearch(query);
    }
});


